#process{
	padding: 20px;
	width: 220px;
	margin: auto;
	background-color: white;
	border-radius: 0px 0px 5px 5px;
}
h3{
	margin: unset;
}
#process > ol{
	padding: 0px 0px 0px 25px;
	margin: 5px 0px 0px 0px;
}
#process > ol > li{
	color: #7851a9;
}
#process > ol > li > p:before{
	content: "-|";
	color: #7851a9;
}
#process > ol > li > p{
	content: "-|";
	color: black;
}
#ownership{
	width: 258px;
	padding: 2px 15px;
	margin: auto;
	background-color: white;
	border-radius: 5px;
	margin-top: 7px;
	box-sizing: border-box;
	font-size: 14px;
}
#instaLink{
	color: #7851a9;
	background-color: rgb(229,229,234);
	border-radius: 5px;
	padding: 0px 5px;
	display: inline-block;
}