#card{
	padding: 20px;
	width: 220px;
	margin: auto;
	border-radius: 0px 0px 15px 15px;
	position: relative;

	background-color: white;
}
#productPrice:before{
	content: "$";
}
#imgCont{
	display: grid;
	grid-template-columns: 1fr 3fr;
	position: relative;
}
#contImg{
	width: 165px;
	height: 240px;
	overflow: hidden;
}
#productImage{
	width: 100%;
}
#productDescription{
	width: 180px;
	height: 40px;
	transform: rotate(270deg);
	position: absolute;
	bottom: 70px;
	left: -70px;
	text-transform: uppercase;
	font-size: 12px;
}
#lowerPortion{
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin: 50px 0px 0px 40px;
}
#color{
	display: grid;
	grid-template-columns: repeat(3, 12px);
	grid-gap: 5px;
	position: relative;
}
#containerTitle{
	position: absolute;
	top: -20px;
	font-size: 14px;
}
#color > div {
	width: 12px;
	height: 12px;
	border-radius: 50%;
	overflow: hidden;
}
#color > div >input{
	border: unset;
	background-color: unset;
	padding: unset;
	width: 20px;
	height: 25px;
	margin: -6px -4px;
	cursor: pointer;
}
#quantity{
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	text-align: center;
	position: relative;
	align-items: center;
	width: 70px;
	cursor: pointer;
}
#quantity > div{
	border: 1px solid black;
	height: 16px;
	/*width: 16px;*/
	display: grid;
	align-items: center;
	font-size: 14px;
}
#quantity > div:nth-child(3){
	border-left: unset;
	border-right: unset;
	cursor: default;
}
#activeColor{
	outline: 1.5px solid #7851a9;
	outline-offset: 1.5px;
}
.activeColor{
	outline-offset: 1.5px;
}